// Custom Theming for Angular Material
// For more information: https://material.angular.io/guide/theming
@use '@angular/material' as mat;
@import "./assets/scss/var.scss";
@import"./assets/scss/palette.scss";
@import "bootstrap";
// Plus imports for other components in your app.

// Include the common styles for Angular Material. We include this here so that you only
// have to load a single css file for Angular Material in your app.
// Be sure that you only ever include this mixin once!
@include mat.core();

// Define the palettes for your theme using the Material Design palettes available in palette.scss
// (imported above). For each palette, you can optionally specify a default, lighter, and darker
// hue. Available color palettes: https://material.io/design/color/
$ecclesia-frontend-primary: mat.define-palette($primary_palette);
$ecclesia-frontend-accent: mat.define-palette($accent_palette, 600);

// The warn palette is optional (defaults to red).
$ecclesia-frontend-warn: mat.define-palette(mat.$red-palette);

// Create the theme object. A theme consists of configurations for individual
// theming systems such as "color" or "typography".
$ecclesia-frontend-theme: mat.define-light-theme((
  color: (
    primary: $ecclesia-frontend-primary,
    accent: $ecclesia-frontend-accent,
    warn: $ecclesia-frontend-warn,
  )
));


// Include theme styles for core and each component used in your app.
// Alternatively, you can import and @include the theme mixins for each component
// that you are using.
@include mat.all-component-themes($ecclesia-frontend-theme);
@import "./assets/css/pixeladmin.min.css";


@import url("https://fonts.googleapis.com/icon?family=Material+Icons|Material+Icons+Outlined");
@import "../node_modules/angular-calendar/css/angular-calendar.css";
body{
    margin:0;
}
html, body { height: 100%; }
h2, .h2 {
  font-size: 1.5rem!important;
}
body { margin: 0; font-family: Roboto, "Helvetica Neue", sans-serif; }

.mat-mdc-header-row {
  font-size: 14px;
  color: #fff;
}

.table-style-1{
  tr.mat-header-row {
    background-color: $color3;
    color: #fff;
}
}
.mdc-text-field--filled:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-floating-label, .mdc-text-field--filled:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-floating-label--float-above {
  color: #2e2e2e!important;
}
.table3{
  padding: 20px;
  .mat-mdc-header-row{
      background-color: $color3;
      color: #fff;
      .mat-icon{
          cursor: pointer;
      }
  }
  .actions-header{
      display: flex;
      align-items: center;
      justify-content: space-around;
      span{
          min-width: 70px;
      }
      .mat-icon{
        background-color: #fff;
        border-radius: 50%;
        color: $color3;
      }

      
  }
  .icons-actions{
      .mat-icon{
          color: $color3;
      }

  }
  .page{padding-top: 15px;}
}

