/**
CALENDAR
**/

.btn-primary {
  @include button-variant($color1, darken($color1, 7.5%), darken($color1, 10%), lighten($color1,5%), lighten($color1, 10%), darken($color1,30%));
}
.btn-primary {
  color: #fff;
  background-color: $color1;
  border-color: $color1;
}

.error-snackbar {
  --mat-mdc-snack-bar-button-color: #fff;
  --mdc-snackbar-container-color: #d9534f;
  --mdc-snackbar-supporting-text-color: rgba(255, 255, 255, 0.87);
}

.success-snackbar {
    --mat-mdc-snack-bar-button-color: #fff;
    --mdc-snackbar-container-color: #53a653;
    --mdc-snackbar-supporting-text-color: rgba(255, 255, 255, 0.87);
  
}

.warning-snackbar {
  --mat-mdc-snack-bar-button-color: #fff;
  --mdc-snackbar-container-color: #dac508;
}

.info-snackbar {
  --mat-mdc-snack-bar-button-color: #fff;
  --mdc-snackbar-container-color: #aaa;
}



@media print{
  body {-webkit-print-color-adjust: exact;}
  .label-ingreso, .label-egreso{background-color: transparent!important;}
  .media-only{display:block !important;}
  .container{
    max-width: none!important;
    padding: 0px 0px!important;
    
    padding-bottom: 40px!important;
  }
  .print-only{
    display: block;
  }
  .mat-elevation-z8{
    box-shadow: none!important;
  }
  .mat-mdc-cell {
    font-size: 11px!important;
    line-height: normal!important;
}
  nav{
    display: none!important;
    
  }
  .no-media{
    display: none !important;
  }
  .mat-mdc-header-row{
    background-color: $color3!important;
    color: #fff!important;
  }
  .col-md-6{
    float: left;
    width: 48%;
    margin-left: 1%;
  }
  .header{
    padding: 0px 0px!important ;
    .mat-icon{
      display: none!important;
    }
  }
  tr.mat-mdc-row, tr.mat-footer-row{
    height: 4px!important;
    page-break-inside: avoid;
  }
  tr.mat-mdc-header-row {
    height: 22px!important;
    background-color: transparent!important;
    border-style: hidden!important;
  }
  .mat-mdc-header-row{
    color: #000!important;
    font-weight: 600;
    background-color: #fff!important;
  
  }
  .category{
    font-weight: 600!important;
  }
  table label{
    color: #000!important;
  }
  .mat-typography h2, h3, h4, h5{
    font-size: 14px!important;
    margin-bottom: 0px!important;
  }
  .page{
    margin-top: 0px!important;
    padding-top: 0px!important;
    margin-bottom: 0px!important;
  }
  table td{
    padding: 0px!important;
  }
  .tables-container{
    padding: 0px 0px!important;
    
  }
  .table3{
    padding-bottom: 0px!important;
  }
}